body {
  font-family: "poppins", sans-serif;
  background: linear-gradient(to bottom, #A2C4E6 10%, #DDEAF6 90%);
  overflow-x: hidden;
  margin: 0;
}

.main-content {
  padding: 40px;
  margin: 0 auto;
  width: 100%;
  max-width: 1200px;
}


.App {
  width: 100%;
  overflow-x: hidden;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}